import { createI18n } from 'vue-i18n'
import EN from './en'
import ZH from './zh'
const messages = {
    en: {
        ...EN
    },
    zh: {
        ...ZH
    }
}
// 返回当前浏览器设置的语言 zh en
const getCurrentLanguage = () => {
    const UAlang = navigator.language // zh-CN  输出当前浏览器设置的语言
    const langCode = UAlang.indexOf('zh') !== -1 ? 'zh' : 'en' // str.indexOf() : 判断str中是否含有特定的字符，没有的话输出-1
    localStorage.setItem('lang', langCode)
    return langCode
}

const i18n = createI18n({
    legacy: false,
    globalInjection: true,
    locale: getCurrentLanguage() || 'zh',
    messages: messages
})

export default i18n
